/**
 * æ·±åº¦åˆå¹¶ï¼ˆç®€å•ç‰ˆï¼‰
 * æ³¨ï¼šä»…åˆå¹¶å¯¹è±¡ä¸­çš„è‡ªèº«å¯è¿­ä»£å±žæ€§ï¼Œå…¶ä»–çš„éƒ½ä¸åˆå¹¶ï¼Œåˆå¹¶è§„åˆ™å¦‚ä¸‹ï¼š
 * booleanã€numberã€stringã€nullã€undefinedã€Array å’Œ function ç±»åž‹é‡‡ç”¨è¦†ç›–çš„æ–¹å¼åˆå¹¶ï¼Œ
 * é™¤æ­¤ä»¥å¤–çš„å¼•ç”¨ç±»åž‹ï¼Œä½¿ç”¨æ·±åº¦åˆå¹¶ã€‚
 * @param { Object * n}  è¢«åˆå¹¶å¯¹è±¡ï¼ŒåŽè€…åˆå¹¶ä¼˜å…ˆçº§é«˜äºŽå‰è€…
 * @returns æ·±åº¦åˆå¹¶åŽçš„å¯¹è±¡
 */
export const deepMerge = (...sources) => {
    const target = {}
    for (let i = 0; i < sources.length; i++) {
        const source = sources[i]
        for (let key in source) {
            if (!source.hasOwnProperty(key)) {
                continue
            }
            const value = source[key]
            if (typeof value !== 'object' || value === null || value.constructor === Array) {
                target[key] = value
            } else {
                target[key] = deepMerge(target[key], value)
            }
        }
    }
    return target
}

/**
 * æ·±åº¦æ‹·è´ï¼ˆç®€å•ç‰ˆï¼‰
 * æ³¨ï¼šä»…æ‹·è´å¯¹è±¡ä¸­çš„è‡ªèº«å¯è¿­ä»£å±žæ€§ï¼Œå…¶ä»–çš„éƒ½ä¸æ‹·è´ï¼Œæ‹·è´è§„åˆ™å¦‚ä¸‹ï¼š
 * booleanã€numberã€stringã€nullã€undefined å’Œ function ç±»åž‹é‡‡ç”¨æµ…æ‹·è´ï¼Œ
 * é™¤æ­¤ä»¥å¤–çš„å¼•ç”¨ç±»åž‹ï¼ˆå¯¹è±¡å’Œæ•°ç»„ï¼‰ï¼Œä½¿ç”¨æ·±æ‹·è´ã€‚
 * @param { Array | Object } source è¢«æ‹·è´å¯¹è±¡
 * @returns æ·±åº¦æ‹·è´åŽçš„å¯¹è±¡
 */
export const deepCopy = (source) => {
    const target = {}
    const deepCopyArray = (array) => {
        return array.map(item => {
            if (typeof item !== 'object' || item === null) {
                return item
            } else if (item.constructor === Array) {
                return deepCopyArray(item)
            } else {
                return deepCopy(item)
            }
        })
    }
    for (let key in source) {
        if (!source.hasOwnProperty(key)) {
            continue
        }
        const value = source[key]
        if (typeof value !== 'object' || value === null) {
            target[key] = value
        } else if (value.constructor === Array) {
            target[key] = deepCopyArray(value)
        } else {
            target[key] = deepCopy(value)
        }
    }
    return target
}
